<template>
    <div class="push-book">
        <div>
            <table>
                <thead>
                    <th colspan="2">输入需要购买的心愿书本信息</th>
                </thead>
                <tbody>
                    <tr v-for = "item in data" :key="item.name">
                        <td>{{item.name}}</td>
                        <td>
                            <input ref='input' type="text" :placeholder="item.text" @blur="onBlur" @input="onInput" :id="item.id" v-model="item.info">
                        </td>
                    </tr>
                    
                </tbody>
            </table>
             
        </div>
        <button @click="tClick">确认添加</button>
        <span v-for="(err, index) in errors" :key="index" style="display:block;color:red;">{{err}}</span>
    </div>
</template>

<script>
export default {
    name:'pushBook',
    data(){
        return {
            bookname:'',
            bookdate:'',
            bookprice:'',
            data:[
                {
                    id:1,
                    name: 'name:',
                    text: '请输入书本的名字',
                    info: this.bookname1
                },
                {
                    id:2,
                    name: 'date:',
                    text: '请输入计划购买此书时间',
                    info: this.bookdate
                },
                {
                    id:3,
                    name: 'price:',
                    text: '请输入书本的价格',
                    info: this.bookprice
                }
            ],
            newBook:{
                    name:'JAVA',
                    date:'1999-02',
                    price:88,
                    count:1
            }
            
        }
    },
    computed: {
        errors() {
            console.log(this.$vuerify);
            return this.$vuerify.$errors // 错误信息会在 $vuerify.$errors 内体现
        }
    },
    vuerify: {
        'bookname':{
            test: function(){
              if(!this.bookname) {
                  return false
              }else {
                  return true
              }
            } , // 自定义规则，可以是函数，正则或者全局注册的规则（填字符串）
            message: '请输入书名',
            
        },
        'bookdate': {
            test:/\d{4}-\d{2}-\d{2}/,
            message:'请按照xxxx-xx-xx的格式填写日期'
        },
        'bookprice':{
            test:/^\d|\d$/,
            message:'请输入规范的书本价格'
        }
    },
    methods:{
        tClick() {
            
            
            // console.log(this.$store.state.books)

            
       
            if (this.$vuerify.check()) { // 手动触发校验所有数据
                // do something
                alert('加入成功');

                const book = {
                id:++this.$store.state.counter,
                name:this.newBook.name,
                date:this.newBook.date,
                price:parseInt(this.newBook.price),
                count:1
                }

                
                this.$emit('pushClick',true)
                this.$emit('newBooksClick',book)

                console.log(this.$refs.input)
                for(let input of this.$refs.input){
                    input.value = ''
                }
            } else {
                alert('加入失败，请按照要求填写心愿书的信息！');
            }
            // console.log(this.bookname)
            
        },
        onBlur() {
            console.log(event.target.id)
            /* if(event.target.id == 1){
                this.newBook.id = event.target.value
            }else  */if(event.target.id == 1) {
                this.newBook.name = event.target.value
            }else if(event.target.id == 2) {
                this.newBook.date = event.target.value
            }else{
                this.newBook.price =event.target.value
            }
        },
        onInput(){
            this.bookname = this.data[0].info
            this.bookdate = this.data[1].info
            this.bookprice = parseInt(this.data[2].info)
        }
    }
}
</script>

<style scoped>
   table{
       border:1px solid #e9e9e9;
       border-collapse:collapse;
       border-spacing: 0;
       margin: 20px auto;
   }
   td,th{
       padding:8px 16px;
       border:1px solid #e9e9e9;
       text-align: left;
   }
   th{
       background-color:#f7f7f7;
       color:#5c6b77;
       text-align: center;
   }
</style>