<template>
  <div class="home">
    <h2>记录你想买的书籍</h2>
    <div v-if="booksLength">
         <table>
            <thead>
                <tr>
                    <th></th>
                    <th>书籍名称</th>
                    <th>计划买书的日期</th>
                    <th>价格</th>
                    <th>购买数量</th>
                    <th>本书总价格</th>
                    <th>操作</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="(item,index) in books" :key="item.id">
                    <td>{{item.id}}</td>
                    <td>{{item.name}}</td>
                    <td>{{item.date}}</td>
                    <td>{{item.price | filterPrice}}</td>
                    <td>
                        <button @click="btnClick1(item)" :disabled="item.count < 2">-</button>
                        {{item.count}}
                        <button @click="btnClick2(item)">+</button>
                    </td>
                    <td>{{item.count*item.price | filterPrice}}</td>
                    <td>
                        <button @click="removeClick(index)">移除</button>
                    </td>
                </tr>
            </tbody>
         </table>
         <h2>完成心愿所需的总价格：{{computPrice | filterPrice}}</h2>
     </div>
     <div v-else>购物车为空</div>
     <button @click="pushBookClick" class="btn" v-show="isShow">添加书籍</button>
     <push-book v-show="!isShow" @pushClick="tClick" @newBooksClick="newBooksClick"/>
  </div>
</template>

<script>
import pushBook from './compuChilds/pushBook'
import {mapMutations,mapGetters} from 'vuex'

export default {
  name: 'Home',
  data(){
    return {
      isShow:true
    }
  },
  methods:{
      ...mapMutations(['newBooksClick','removeClick']),
      btnClick1(item){
          item.count--
      },
      btnClick2(item){
          item.count++
      },
      pushBookClick() {
          this.isShow = false
      },
      tClick(boo) {
        // console.log(boo)
        this.isShow = boo
      }
                
  },
  computed:{
      computPrice(){
          let result = 0
          for(let book of this.books){
              result += book.price * book.count
          }
              return result
      },
      ...mapGetters(['books','booksLength'])
    
  },
  filters:{
      filterPrice(price){
          return '￥'+ price.toFixed(2)
      }
  },
  components: {
    pushBook
  }
}
</script>

<style scoped>
  table{
       border:1px solid #e9e9e9;
       border-collapse:collapse;
       border-spacing: 0;
       margin:0 auto;
   }
   td,th{
       padding:8px 16px;
       border:1px solid #e9e9e9;
       text-align: left;
   }
   th{
       background-color:#f7f7f7;
       color:#5c6b77;
   }
   .btn {
     color:#fff;
     background-color: green;
     border:none;
     width:150px;
     height:37px;
     font-size:20px;
     margin-bottom:30px;
   }
